import { useEffect } from 'react';

/**
 *  Hook props 
 * @param {string} componentName - 
 * @param {string} oldProp -  prop 
 * @param {string} newProp -  prop 
 */
export const useDeprecationWarning = (componentName, oldProp, newProp) => {
  useEffect(() => {
    if (process.env.NODE_ENV !== 'production' && oldProp !== undefined) {
      console.warn(
        `Warning: The prop "${oldProp}" will be deprecated. ` +
          `you can use "${newProp}" instead.` +
          `\n\nPlease update the following components: ${componentName}`,
      );
    }
  }, [componentName, oldProp, newProp]);
};

/**
 *  Hook props 
 * @param {string} componentName - 
 * @param {string} propName -  prop 
 * @param {string} propType -  prop 
 * @param {string} expectedType -  'object', 'number' 
 */
export const useTypeChangeWarning = (
  condition,
  componentName,
  propName,
  propType,
  expectedType,
) => {
  useEffect(() => {
    if (process.env.NODE_ENV !== 'production') {
      if (condition) {
        console.warn(
          `Warning: The prop "${propName}" is expected to be of type "${expectedType}", ` +
            `but received type "${propType}". ` +
            `\n\nPlease update the following components: ${componentName}`,
        );
      }
    }
  }, [condition, componentName, propName, propType, expectedType]);
};
