import type { Locale } from '../config-provider/interface';

export enum REFRESH_STATE {
  normal, // 
  pull, // 
  drop, // 
  loading, // 
  success, // 
  failure, // 
}

export enum LOAD_STATE {
  normal, // 
  abort, // 
  loading, // 
  success, // 
  failure, // 
  complete, // 
}

export interface PullAction {
  state?: REFRESH_STATE | LOAD_STATE;
  startDistance?: number;
  distance?: number;
  render?: (state?: REFRESH_STATE | LOAD_STATE, percent?: number) => any;
  handler?: () => void;
}

export interface BasePullProps {
  refresh?: PullAction;
  load?: PullAction;
  animationDuration?: number;
  stayTime?: number;
  locale?: Locale['Pull'];
}
